


















































































































































































.menu-tree {
  min-width: 120px;
  .ant-menu-item {
    .action-edit, .action-delete {
      display: none;
    }
    &:hover {
      .action-edit, .action-delete {
        display: block;
      }
    }
  }
  .ant-menu-submenu-title {
    .action-add {
      display: none;
    }
    &:hover {
      .action-add {
        display: block;
      }
    }
  }
  .action-add, .action-edit, .action-delete {
    z-index: 1200;
    position: absolute;
    height: 40px;
    line-height: 40px;
    bottom: 0px;
    right: 30px;
    display: block;
    color: #1890ff
  }
  
   .action-delete {
    right: 6px;
  }
  
}

