.menu-tree {
  min-width: 120px;
}
.menu-tree .ant-menu-item .action-edit,
.menu-tree .ant-menu-item .action-delete {
  display: none;
}
.menu-tree .ant-menu-item:hover .action-edit,
.menu-tree .ant-menu-item:hover .action-delete {
  display: block;
}
.menu-tree .ant-menu-submenu-title .action-add {
  display: none;
}
.menu-tree .ant-menu-submenu-title:hover .action-add {
  display: block;
}
.menu-tree .action-add,
.menu-tree .action-edit,
.menu-tree .action-delete {
  z-index: 1200;
  position: absolute;
  height: 40px;
  line-height: 40px;
  bottom: 0px;
  right: 30px;
  display: block;
  color: #1890ff;
}
.menu-tree .action-delete {
  right: 6px;
}

